/*****************************************************************************/
/*
/* Tufte Jekyll blog theme
/* Based on Tufte CSS by Dave Liepmann ( https://github.com/edwardtufte/tufte-css )
/*
/* The README.md will show you how to set up your site along with other goodies
/*****************************************************************************/

// Imports to create final

@import "../_sass/fonts";
@import "../_sass/settings";
@import "../_sass/syntax-highlighting";

// disqus

#disqus_box {
  padding-top: 2em;
  width: $column-width;
}
@media screen and (max-width: 760px){ #disqus_box {width: 100%; } }


// Global Resets
//
* { margin: 0; padding: 0; }

/* clearfix hack after Cederholm (group class name) */
.group:after {
  content: "";
  display: table;
  clear: both;
}

html, body { height: 100%; }

// First significant deviation from CSS on tufte.css site - variable font size as browser width expands or contracts
//html { font-size: 15px; }

html{
    vertical-align: baseline;
    font-size: 11px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

  }

/*@media screen and (min-width: 800px){ html{ font-size: 12px;} }*/
@media screen and (min-width: 760px){ html{ font-size: 13px;} }

@media screen and (min-width: 1000px){ html{ font-size: 14px;} }

@media screen and (min-width: 1100px){ html{ font-size: 15px; } }

/*
@media screen and (min-width: 1200px){ html{ font-size: 16px; } }

@media screen and (min-width: 1300px){ html{ font-size: 17px; } }
*/

// TOC TABLE OF CONTENTS

.toc-heading {
  //font-variant: small-caps;
  font-style: italic;
  font-size: 1.6rem;
}

#toc {
  background-color: $box-color;
  border:1px dashed $contrast-color;
  width: $column-width;
  padding: 10px;
  margin-top: 10px;

  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
}

@media screen and (max-width: 760px){ #toc {width: 100%; } }

#toc ul {
  font-size: 1.2rem;
  width: auto
}

#toc ul li {
  line-height: 1.5rem;
}

#toc ul ul {
  list-style-type: none;
  margin-top: 0.2em;
  padding-left: 3em;
}

#toc ul ul:before {
  content: "↳";
  margin-left: -1.2em
}


#toc ul ul :not(:first-of-type):before {
  content: "— ";
}

#toc ul ul li {
  display: inline
}


// sets link style according to values in _settings.scss
.mathblock{
  font-size: 1.3rem;
}
a {
  color: $contrast-color;
  text-decoration: none;
}

p > a { @if $link-style == underline
    {
      color: $text-color;
      text-decoration: none;
      border-bottom: 1px solid #777;
      padding-bottom: 1px;
    }
    @else
    {
      color: $contrast-color;
      text-decoration: none;
    }
  }

body { width: 95%;
       margin-left: auto;
       margin-right: auto;
       /*padding-left: 6%;*/
       font-family: et-bembo, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, serif;
       background-color: $bg-color;
       color: $text-color;
       max-width: 1100px;
       counter-reset: sidenote-counter; }

@media screen and (max-width: 760px){ body{ width: 95%;} }
      

// --------- Typography stuff -----------//
// added rational line height and margins ala http://webtypography.net/intro/

h1 { font-weight: 400;
     margin-top: 1.568rem;
     margin-bottom: 1.568rem;
     font-size: 2.5rem;
     /*line-height: 0.784; */
    }

h2 { font-style: italic;
     font-weight: 400;
     margin-top: 1.866666666666667rem;
     margin-bottom: 0;
     font-size: 2.2rem; //2.1
     line-height: 0.933333333333333; }

h3 { font-style: italic;
     font-weight: 400;
     font-size: 1.7rem; //1.8
     margin-top: 2.1777777777777778rem;
     margin-bottom: 0;
     line-height: 1.08888888888889; }

// ET says a need for more than 3 levels of headings is the sign of a diseased mind

p .subtitle { font-style: italic;
             margin-top: 2.1777777777777778rem;
             margin-bottom: 2.1777777777777778rem;
             font-size: 1.8rem;
             display: block;
             line-height: 1.08888888888889; }

div.date-and-lang {
  margin-top: 0.2rem;
  width: 100%;
  line-height: 1rem;
  display: flex;
}

p, ol, ul { font-size: 1.4rem; }

p, li { line-height: 2rem;
        margin-top: 1.4rem;
        //padding-right: 2rem; //removed because,  why?
        vertical-align: baseline; 
      }

blockquote p {  font-size: 1.6rem;
                line-height: 1.78181818;
                margin-top: 1.78181818rem;
                margin-bottom: 1.78181818rem;
                width: 55%;
                padding-left: 2.5%;
                padding-right: 2.5%; 
                font-style: italic;}

blockquote footer { width: 45%;
                    text-align: right; }

sup {
  font-size: 0.8rem;
  line-height: 1;
}

.sans { font-family: $sans-font;
        letter-spacing: .03em; }

pre, pre code, p code, li code, p pre code { font-family: $code-font;  // removed .code 'class' since code is an actual html tag
        font-size: 1.2rem;                   // also added p code, p pre code and pre selector to account for Markdown parsing
        line-height: 1.71428571;        // of triple backticks plus rationalized line-heights and margins
        margin-top: 1.71428571rem; 
        background-color: $box-color;
      }

pre {
  border: 1px solid #dbd5b2;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  border-radius: 4px;
  padding:2px;
}

code.highlighter-rouge {
  background-color: $box-color;
  padding: 2px;
  border: 1px solid #dbd5b2;
  border-radius: 4px;
}


h1 code, h2 code, h3 code { font-size: 0.80em; } //left in for no real reason

/*-- Table styling section - For Tufte-Jekyll, booktabs style is default for Markdown tables  --*/

table, table.booktabs { width: auto;  //making booktabs style tables the unstyled default in case someone uses Markdown styling
                 margin: 0 auto;
                 border-spacing: 0px;
                 border-top: 2px solid $border-color;
                 border-bottom: 2px solid $border-color; }

.booktabs th, th { border-bottom: 1px solid $border-color;
               padding: 0.65ex 0.5em 0.4ex 0.5em;
               font-weight: normal;
               text-align: center; }

th, td{ font-size: 1.2rem;
        line-height: 1.71428571;  }

.booktabs th.cmid, th { border-bottom: 1px solid lighten($border-color, 25%); }

.booktabs th.nocmid { border-bottom: none; }

.booktabs tbody tr:first-child td,  tr:first-child td { padding-top: 0.65ex; } /* add space between thead row and tbody */

.booktabs td, td {  padding-left: 0.5em;
                    padding-right: 0.5em;
                    text-align: left; }

.booktabs caption, caption { font-size: 90%;
                    text-align: left;
                    width: auto;
                    margin-left: auto;
                    margin-right: auto;
                    margin-top: 1ex;
                    caption-side: top; }

.booktabs tbody tr td.l { text-align: left !important; }
.booktabs tbody tr td.c { text-align: center !important; }
.booktabs tbody tr td.r { text-align: right !important; }

.table-caption { float:right;
                 clear:right;
                 margin-right: -60%;
                 width: 50%;
                 margin-top: 0;
                 margin-bottom: 0;
                 font-size: 1.0rem;
                 line-height: 1.96; }
/* -- End of Table Styling section --*/


/* Basic Layout stuff --*/

  /* index page stuff */ 
  main.indexpage {
    margin-top: 20px;
    grid-area: m;
  }

  .indexpage-sidebar {
    grid-area: n;
    background: $box-color;
    margin: 0 0 0 1rem;
    padding: 1rem;
  }

  .indexpage-sidebar p {
    width: 100%;
    font-size: 1rem;
    line-height: 1.3rem;
  }

  .indexpage-sidebar h1 {
    margin-top: 0.5rem;
  }

  body.indexpage #container {
    display: grid;
    grid-template-columns: auto 300px;
    grid-template-rows: 100%;
    grid-template-areas: 
      "m n";
  }

  li.listing-final {
    text-align: center;
    font-size: 1.1rem;
  }

  @media screen and (max-width: 760px) {
    body.indexpage #container {
      display: grid;
      grid-template-columns: 100%;
      grid-template-rows: auto auto;
      grid-template-areas: 
        "m"
        "n";
    }

    .indexpage-sidebar {
      grid-area: n;
      margin: 0;
      padding: 1rem;
    }

    .indexpage-sidebar img {
      max-width: 400px;
    }

    .indexpage-sidebar p {
      font-size: 1.5rem;
      line-height: 2rem;
    }

    li.listing-final {
      display: none;
    }
  }

  /* end of index page */

article { position: relative;
          padding: 1

          rem 0rem 2.5rem 0rem; } // reduced top and bottom padding by 50%

section { padding-top: 1rem;
          padding-bottom: 1rem; }


ul,ol {
    width: $column-width - 5%;
    padding-left: 5%;
    /*list-style-type: none; */
    }

li ul ,li ol {
  width: 95%;
  padding-left: 5%;
  /*list-style-type: none; */
}


li { margin: 0.5em 0; } //vertical padding on list items screws up vertical rhythym

figure, figure img.maincolumn { 
  width: $column-width;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  margin-bottom: 1em; 
}

.highlight {
  width: $column-width;
  background-color: $box-color;
}

.sidenote, .marginnote, figcaption { float: right;
  clear: right;
  margin-right: -66.6%;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.0rem;
  line-height: 1.6;
  vertical-align: baseline;
  position: relative;
  width: 60%; 
}

@media screen and (max-width: 760px) {
  figure { display: table; width: 100%;}
  figure img { display: block; }
  figure figcaption { 
    display: table-caption; 
    caption-side: bottom; 
    text-align: center; 
    margin-right: 0; 
    margin-left: 0; 
    width: 100% !important;
  }
  }

figure.fullwidth { display: table; }
figure.fullwidth img { display: block; }
figure.fullwidth figcaption { 
  display: table-caption; 
  caption-side: bottom; 
  text-align: center; 
  margin-right: 0%; 
  margin-left: 0%; 
  width: 100% !important;
}

img { max-width: 100%; }

li .sidenote, li .marginnote{ margin-right: -80%; } //added to allow for the fact that lists are indented and marginnotes and sidenotes push to right

blockquote .sidenote, blockquote .marginnote { margin-right: -72%; font-style: normal; }

.sidenote-number { counter-increment: sidenote-counter; }

.sidenote-number:after, .sidenote:before { content: counter(sidenote-counter) " ";
                                           font-family: et-bembo-roman-old-style;
                                           color: $contrast-color; //added color
                                           position: relative;
                                           vertical-align: baseline; }

.sidenote-number:after { content: counter(sidenote-counter);
                         font-size: 1rem;
                         top: -0.5rem;
                         left: 0.1rem; }

.sidenote:before { content: counter(sidenote-counter) ".\000a0"; // this is unicode for a non-breaking space
                   color: $contrast-color;
                   top: 0rem; } //removed superscripting for numerical reference in sidenote

p, div.table-wrapper, div.mathblock { width: $column-width; }

div.table-wrapper { overflow-x: auto; } //changed all overflow values to 'auto' so scroll bars appear only as needed

@media screen and (max-width: 760px) { p, footer,div.mathblock { width: 100%; }
                                       pre code, .highlight { width: 100%; }
                                       ul, ol { width: 90%; }
                                       figure { max-width: 100%; }
                                       blockquote p, blockquote footer {  width: 80%;
                                                                          padding-left: 5%;
                                                                          padding-right: 5%;
                                                                        }}

.marginnote code, .sidenote code { font-size: 1rem; } //more .code class removal

pre { width: 52.5%;
           /*padding-left: 2.5%;*/
           overflow-x: auto; }

.fullwidth, li.listing div{ width: 100%; }

.full-width { .sidenote, .sidenote-number, .marginnote { display: none; } }

span.newthought { font-variant: small-caps;
                  font-size: 1.2em;
                  letter-spacing: 0.05rem; }

input.margin-toggle { display: none; }

label.sidenote-number { display: inline; }

label.margin-toggle:not(.sidenote-number) { display: none; }

@media (max-width: 760px) { label.margin-toggle:not(.sidenote-number) { display: inline; color: $contrast-color; }
                            .sidenote, .marginnote { display: none; }
                            .margin-toggle:checked + .sidenote,
                            .margin-toggle:checked + .marginnote { display: block;
                                                                   float: left;
                                                                   left: 1rem;
                                                                   clear: both;
                                                                   width: 95%;
                                                                   margin: 1rem 2.5%;
                                                                   vertical-align: baseline;
                                                                   position: relative; }
                            label { cursor: pointer; }
                            pre, pre code, p code, p pre code { width: 90%;
                                       padding: 0; }
                            .table-caption { display: block;
                                             float: right;
                                             clear: both;
                                             width: 98%;
                                             margin-top: 1rem;
                                             margin-bottom: 0.5rem;
                                             margin-left: 1%;
                                             margin-right: 1%;
                                             vertical-align: baseline;
                                             position: relative; }
                            div.table-wrapper, table, table.booktabs { width: 85%; }
                            div.table-wrapper { border-right: 1px solid #efefef; }
                            img { max-width: 100%; } }
/*--- End of Basic Layout stuff from tufte.css ---*/

/* -- Jekyll specific styling --*/
//helper classes

.contrast { color: $contrast-color;}
.smaller { font-size: 80%;}
//Nav and Footer styling area

header {
  border-bottom: 2px solid $contrast-color;
}

header > nav.group {
  width: 95%;
  padding-top: 2rem;
}

nav.group a.active:before{ content:"\0003c\000a0";} // escaped unicode for the carats and then a space on active menu links
nav.group a.active:after{ content:"\000a0\0003e" ;}

header > nav a{
  font-size: 2rem;
  font-family: $sans-font;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: $contrast-color;
  padding-top: 1.5rem;
  text-decoration: none;
  display: inline-block;
  float: left;
  margin-top: 0;
  margin-bottom: 0.4rem;
  padding-right: 2rem;
  //margin-left: 1rem;
  vertical-align: baseline;
}
header > nav a img{
  height: 5rem;
  position: relative;
  max-width: 100%;
  top:-1.5rem;
}
ul.footer-links, .credits{
  list-style: none;
  text-align: center;
  margin: 0 auto;
}
ul.footer-links{
  width: 100%;
  padding: 0;
}
ul.footer-links li{
  display: inline;
  padding: 0.5rem 0.25rem;
}
.credits{
  padding: 1rem 0rem;
}

//change font color for credit links in footer

.credits{
  font-family: $sans-font;
    & a{
      color: $contrast-color;
    }
}

// End of Nav and Footer styling area

//Full width page styling stuff

body.full-width, .content-listing, ul.content-listing li.listing{
       width: 100%;
       margin-left: auto;
       margin-right: auto;
       padding: 0% 0%;

}

body.indexpage  {

}

@media screen and (max-width: 760px) { 
  body.full-width, .content-listing, ul.content-listing li.listing{ 
    width: 100%;
    padding: 0;
  }
}

.listing > a img{
    border:1px dashed $contrast-color;
    margin-top: 1rem;
}

.listing > div p:first-of-type{
  margin-top:0px
}

.full-width article p{
  width: 90%;
}


h1.content-listing-header{
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 1.8rem;
}

li.listing hr{
  width:100%;
}
.listing, .listing h3
{
  display: inline-block;
  margin:0;
}
li.listing {
  margin:0;
  & p{
    width: 100%
  }
}


li.listing:last-of-type{
  border-bottom: none;
  margin-bottom: 1.4rem;
}
li.listing h3.new {
  text-transform: uppercase;
  font-style: normal;
}
hr.slender {
    border: 0;
    height: 1px;
    margin-top: 1.4rem;
    margin-bottom:1.4rem;
    background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:    -moz-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:     -ms-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:      -o-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
}
// End of front listing page stuff


// Printing ands screen media queries

// Does not display a print-footer for screen display
@media screen{
  .print-footer{
    display: none;
  }
}

//printing stuff
@media print {

    * {
    -webkit-transition: none !important;
    transition: none !important;
    }
    *,
    *:before,
    *:after {
        background: transparent !important;
        box-shadow: none !important;
        text-shadow: none !important;
    }
    @page {
        margin: 0.75in 0.5in 0.75in 0.5in;
        orphans:4; widows:2;
    }

    body {
        font-size:  12pt;

    }
    html body span.print-footer{
      font-family: $sans-font;
      font-size: 9pt;
      margin-top: 22.4pt;
      padding-top: 4pt;
      border-top: 1px solid #000;
    }

    thead {
        display: table-header-group;
    }

    tr,
    img {
        page-break-inside: avoid;
    }

    img {
        max-width: 100% !important;
    }

    p,
    h2,
    h3 {
        orphans: 4;
        widows: 4;
    }
    article h2, article h2 h3, article h3, article h3 h4, article h4, article h4 h5 {
        page-break-after: avoid;
    }

    body header , body footer {
      display:none;
    }
}


